.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.character-box {
  width: 50px;
  height: 50px;
  line-height: 50px;
  background-color: lightgray;
  border-radius: 10px;
  margin: 5px;
  font-size: 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.character-box-large {
  width: 200px;
  height: 200px;
  line-height: 200px;
  background-color: lightgray;
  border-radius: 10px;
  margin: 20px 0 20px 0;
  font-size: 100px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

body {
  font-family: 'Noto Sans', sans-serif;
}

.homeText {
  margin-top: 60px;
  font-size: 14pt;
}

.containerInner {
  padding-top: 60px;
}